<template>
  <div class="container section">
    <div class="sectionHeading">
        <h2>Projects</h2>
    </div>
    <div class="project" v-for="(project, index) in projects" :key="index">
        <h2 class="articleTitle">{{ project.projectTitle }}</h2>
        <p>{{ project.projectDescription }}</p>
        <div class="links-grid" v-for="(link, linkindex) in project.links" :key="linkindex">
            <p v-if="link.backend"><a :href="link.backend">Back-End GitHub</a></p>
            <p v-if="link.frontend"><a :href="link.frontend">Front-End GitHub</a></p>
            <p v-if="link.demo"><a :href="link.demo">Demo</a></p>
        </div>
    </div> 
  </div>
</template>

<script>
const axios = require('axios')

export default {
  data() {
    return {
        projects : [] 
    }
  },
  created() {
    axios.get('https://dev.kevinzaworski.com/lib/api/projects').then((response) => this.projects = response.data)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .links-grid {
        align-content: start;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(9rem, 1fr));
        place-items: start;
        justify-content: center;
        gap: 1.5rem 1.5rem;
        margin-top: 0px;
        margin-bottom: 0px;
    }

    .project {
        margin-top: 20px;
    }
</style>
