<template v-if="isLoaded">
  <div class="container section">
    <div class="sectionHeading">
      <h2>Library</h2>
    </div>
    <p>Here is a collection of books that I'm currently reading or have read. Some are related to coding, some are not.</p>
    <div class="books-grid">
      <div v-for="(book, index) in books" :key="index">
        <img :src="book.cover" class="book-cover" />
        <p class="book-title"><strong>{{ book.title }}</strong></p>
        <p>{{ book.author }}</p>
      </div> 
    </div>
  </div>
</template>

<script>
const axios = require('axios');

export default {
  data() {
    return {
      books : [],
      isLoaded : false
    }
  },
  created() {
    axios.get('https://dev.kevinzaworski.com/lib/api/books').then(
      (res) => this.books = res.data
    ).then( this.isLoaded = true )
  }
}
</script>

